import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Drawer from "@mui/material/Drawer";

const dispatchedEvents = [];

const SrsMuiDrawer = function (props, ref) {
  return (
    <Drawer {...props} theme={props.muiTheme}>
      <slot />
    </Drawer>
  );
};

const styles = `
  /* Custom styles for the MuiDrawer component */
`;

const props = [
  {
    name: "anchor",
    defaultValue: "left",
    type: "string",
  },
  {
    name: "open",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "variant",
    defaultValue: "permanent",
    type: "string",
  },
  {
    name: "width",
    defaultValue: 240,
    type: "number",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Drawer if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiDrawer),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
